import React from 'react';
import { motion } from 'framer-motion'; //  Framer Motion
import './HomePage.css'; //    

const HomePage = () => {
    return (
        <div className="homepage">
            {/*   */}
            <motion.header
                className="banner"
                initial={{ opacity: 0, y: -50 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 1 }}
            >
                <h1 className="banner-title">Welcome to WNEVER Agency</h1>
                <p className="banner-subtitle">     </p>
            </motion.header>

            {/*  */}
            <motion.section
                className="instructions"
                initial={{ opacity: 0 }}
                whileInView={{ opacity: 1 }}
                transition={{ duration: 1 }}
            >
                <h2>  ?</h2>
                <div className="steps">
                    <motion.div
                        className="step"
                        whileHover={{ scale: 1.05 }}
                        transition={{ type: 'spring', stiffness: 300 }}
                    >
                        <h3><b></b></h3>
                        <p>     .</p>
                    </motion.div>
                    <motion.div
                        className="step"
                        whileHover={{ scale: 1.05 }}
                        transition={{ type: 'spring', stiffness: 300 }}
                    >
                        <h3><b> </b></h3>
                        <p>     .</p>
                    </motion.div>
                    <motion.div
                        className="step"
                        whileHover={{ scale: 1.05 }}
                        transition={{ type: 'spring', stiffness: 300 }}
                    >
                        <h3><b> </b></h3>
                        <p>      .</p>
                    </motion.div>
                </div>
            </motion.section>

            {/* FAQ */}
            <motion.section
                className="faq"
                initial={{ opacity: 0 }}
                whileInView={{ opacity: 1 }}
                transition={{ duration: 1.5 }}
            >
                <h2><b>FAQ</b></h2>
                <ul>
                    <li><strong>  ?</strong> -  ,  ,    .</li>
                    <li><strong> ?</strong> -  ""      .</li>
                    <li><strong> ?</strong> -   ,   .</li>
                    <li><strong> </strong> - WNEVER Agency    '   .</li>
                </ul>
            </motion.section>

            {/*   */}
            <motion.section
                className="about-us"
                initial={{ opacity: 0, x: -100 }}
                whileInView={{ opacity: 1, x: 0 }}
                transition={{ duration: 1.5 }}
            >
                <h2> </h2>
                <p>( .)</p>
            </motion.section>

            {/*  */}
            <footer className="footer">
                Made by Mykyta Trashchii in 2025
            </footer>
        </div>
    );
};

export default HomePage;
