import React, { useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';

const OrdersModeration = () => {
    const [orders, setOrders] = useState([]);
    const [filteredOrders, setFilteredOrders] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [loading, setLoading] = useState(true);
    const navigate = useNavigate();

    const fetchOrders = async () => {
        try {
            const response = await fetch('/api/orders', {
                method: 'GET',
                headers: { 'Content-Type': 'application/json' },
            });

            if (!response.ok) throw new Error('    ');
            const data = await response.json();
            setOrders(data);
            setFilteredOrders(data); //    
        } catch (error) {
            toast.error(error.message);
        } finally {
            setLoading(false);
        }
    };

    //  
    const handleSearch = (e) => {
        const value = e.target.value.toLowerCase();
        setSearchTerm(value);

        const filtered = orders.filter((order) =>
            order.title.toLowerCase().includes(value) || order.id.toString().includes(value)
        );
        setFilteredOrders(filtered);
    };

    const updateOrderStatus = async (orderId, newStatus) => {
        try {
            const response = await fetch(`/api/orders/${orderId}/status`, {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ status: newStatus }),
            });

            if (!response.ok) throw new Error('    ');
            toast.success('   !');
            fetchOrders(); //   
        } catch (error) {
            toast.error(error.message);
        }
    };

    const deleteOrder = async (orderId) => {
        try {
            const response = await fetch(`/api/orders/${orderId}`, {
                method: 'DELETE',
                headers: { 'Content-Type': 'application/json' },
            });

            if (!response.ok) throw new Error('   ');
            toast.success('  !');
            fetchOrders(); //   
        } catch (error) {
            toast.error(error.message);
        }
    };

    const assignExecutor = async (orderId, executorId) => {
        try {
            const response = await fetch(`/api/orders/${orderId}/executor`, {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ executorId }),
            });

            if (!response.ok) throw new Error('   ');
            toast.success('  !');
            fetchOrders(); //   
        } catch (error) {
            toast.error(error.message);
        }
    };

    useEffect(() => {
        fetchOrders();
    }, []);

    if (loading) return <p> ...</p>;

    return (
        <div>
            <h3> </h3>

            {/*   */}
            <input
                type="text"
                placeholder="  ID  ..."
                value={searchTerm}
                onChange={handleSearch}
                className="form-control mb-3"
            />

            <ul className="list-group">
                {filteredOrders.map((order) => (
                    <li key={order.id} className="list-group-item d-flex justify-content-between align-items-center">
                        ID: {order.id} | {order.title} | : {order.status} |
                        : {order.executor ? `${order.executor.fullName} (ID: ${order.executor.id})` : ' '} |
                        : {order.client ? `${order.client.fullName} (ID: ${order.client.id})` : ' '}
                        <div>
                            {/*     */}
                            <select
                                value={order.status}
                                onChange={(e) => updateOrderStatus(order.id, e.target.value)}
                                className="form-select w-auto d-inline-block me-2"
                            >
                                <option value="PENDING"></option>
                                <option value="APPROVED"></option>
                                <option value="REJECTED"></option>
                                <option value="FINISHED"></option>
                            </select>

                            {/*     */}
                            <button
                                onClick={() => navigate(`/orders/${order.id}`)}
                                className="btn btn-primary btn-sm me-2"
                            >
                                
                            </button>

                            {/*     */}
                            <button
                                onClick={() => assignExecutor(order.id, prompt(' ID '))}
                                className="btn btn-secondary btn-sm me-2"
                            >
                                 
                            </button>

                            {/*    */}
                            <button
                                onClick={() => deleteOrder(order.id)}
                                className="btn btn-danger btn-sm"
                            >
                                
                            </button>
                        </div>
                    </li>
                ))}
            </ul>
        </div>
    );
};

export default OrdersModeration;
