import React, { useEffect, useState, useContext } from 'react';
import { UserContext } from '../UserContext';
import { toast } from 'react-toastify';
import { useNavigate } from 'react-router-dom';
import PageLayout from '../components/PageLayout';
import Modal from 'react-bootstrap/Modal'; //  Bootstrap   
import Button from 'react-bootstrap/Button';
import FinishedOrdersDropdown from '../components/FinishedOrdersDropdown'; //

const OrdersPage = () => {
    const { user } = useContext(UserContext); //   
    const [orders, setOrders] = useState([]); //   
    const [pendingOrders, setPendingOrders] = useState([]); //  PENDING 
    const [assignedOrders, setAssignedOrders] = useState([]); //   
    const [finishedOrders, setFinishedOrders] = useState([]); //   
    const [filteredOrders, setFilteredOrders] = useState([]); //   
    const [searchTerm, setSearchTerm] = useState('');
    const [loadingPending, setLoadingPending] = useState(true); //  PENDING 
    const [loadingAssigned, setLoadingAssigned] = useState(true); //   
    const [showModal, setShowModal] = useState(false); //   
    const [selectedOrderId, setSelectedOrderId] = useState(null); // ID  
    const [note, setNote] = useState(''); //  
    const navigate = useNavigate();

    useEffect(() => {
        if (!user) {
            navigate('/');
            return;
        }

        const fetchOrders = async () => {
            try {
                if (user.role === 'CLIENT') {
                    //    
                    const response = await fetch(`/api/orders/client/${user.id}`, {
                        method: 'GET',
                        headers: { 'Content-Type': 'application/json' },
                    });
                    if (!response.ok) throw new Error('    ');
                    const data = await response.json();
                    setOrders(data);
                    setFilteredOrders(data);
                    const finishedResponse = await fetch(`/api/orders/client/${user.id}/finished`, {
                        method: 'GET',
                        headers: { 'Content-Type': 'application/json' },
                    });
                    if (!finishedResponse.ok) throw new Error('     ');
                    const finishedData = await finishedResponse.json();
                    setFinishedOrders(finishedData);
                } else if (user.role === 'EXECUTOR') {
                    //   PENDING   
                    const pendingResponse = await fetch('/api/orders/pending', {
                        method: 'GET',
                        headers: { 'Content-Type': 'application/json' },
                    });
                    if (!pendingResponse.ok) throw new Error('   PENDING ');
                    const pendingData = await pendingResponse.json();

                    setPendingOrders(pendingData);
                    setLoadingPending(false);

                    const assignedResponse = await fetch(`/api/orders/executor/${user.id}`, {
                        method: 'GET',
                        headers: { 'Content-Type': 'application/json' },
                    });
                    if (!assignedResponse.ok) throw new Error('    ');
                    const assignedData = await assignedResponse.json();
                    setAssignedOrders(assignedData);
                    setLoadingAssigned(false);
                } else if (user.role === 'ADMIN') {
                    //    
                    const response = await fetch('/api/orders', {
                        method: 'GET',
                        headers: { 'Content-Type': 'application/json' },
                    });
                    if (!response.ok) throw new Error('    ');
                    const data = await response.json();
                    setOrders(data);
                    setFilteredOrders(data);
                } else {
                    throw new Error('      ');
                }
            } catch (error) {
                toast.error(error.message);
            }
        };

        fetchOrders();
    }, [user]);

    //  
    const handleSearch = (e) => {
        const value = e.target.value.toLowerCase();
        setSearchTerm(value);

        const filtered = orders.filter((order) =>
            order.title.toLowerCase().includes(value) || order.id.toString().includes(value)
        );
        setFilteredOrders(filtered);
    };

    //   
    const handleOpenModal = (orderId) => {
        setSelectedOrderId(orderId);
        setShowModal(true);
    };

    //   
    const handleCloseModal = () => {
        setShowModal(false);
        setSelectedOrderId(null);
        setNote('');
    };

    //     
    const handleRequestSubmit = async () => {
        try {
            const response = await fetch('/api/requests/create', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ executorId: user.id, orderId: selectedOrderId, note }),
            });

            if (!response.ok) throw new Error('   ');
            toast.success('  ');
            handleCloseModal(); //      
        } catch (error) {
            toast.error(error.message);
        }
    };

    if (!user) return null;

    return (
        <PageLayout title=" ">
            <input
                type="text"
                placeholder="  ID  ..."
                value={searchTerm}
                onChange={handleSearch}
                className="form-control mb-3"
            />
            {user.role === 'EXECUTOR' ? (
                <>
                    {/*  PENDING  */}
                    <div className="mb-5">
                        <h3>PENDING </h3>
                        {loadingPending ? (
                            <p>...</p>
                        ) : pendingOrders.length > 0 ? (
                            <ul className="list-group">
                                {pendingOrders.map((order) => (
                                    <li key={order.id} className="list-group-item d-flex justify-content-between align-items-center">
                                        <div>
                                            <strong>{order.title} (ID: {order.id})</strong>
                                            <p>{order.description.slice(0, 20)}...</p>
                                            <p>: {order.deadline}</p>
                                        </div>
                                        <button className="btn btn-primary" onClick={() => handleOpenModal(order.id)}>
                                             
                                        </button>
                                    </li>
                                ))}
                            </ul>
                        ) : (
                            <p>PENDING  .</p>
                        )}
                    </div>

                    {/*    */}
                    <div>
                        <h3> </h3>
                        {loadingAssigned ? (
                            <p>...</p>
                        ) : assignedOrders.length > 0 ? (
                            <ul className="list-group">
                                {assignedOrders.map((order) => (
                                    <li key={order.id} className="list-group-item d-flex justify-content-between align-items-center">
                                        <div>
                                            <strong>{order.title} (ID: {order.id})</strong>
                                            <p>{order.description.slice(0, 20)}...</p>
                                            <p>: {order.deadline}</p>
                                        </div>
                                        <button className="btn btn-secondary" onClick={() => navigate(`/orders/${order.id}`)}>
                                            
                                        </button>
                                    </li>
                                ))}
                            </ul>
                        ) : (
                            <p>  .</p>
                        )}
                    </div>
                    <FinishedOrdersDropdown finishedOrders={finishedOrders} navigate={navigate} />
                </>
            ) : (
                <>
                    {/*      */}
                    {filteredOrders.length > 0 ? (
                        <ul className="list-group">
                            {filteredOrders
                                .filter((order) => order.status !== 'FINISHED') //     FINISHED
                                .map((order) => (
                                    <li key={order.id} className="list-group-item d-flex justify-content-between align-items-center">
                                        <div>
                                            <strong>ID: {order.id}</strong>
                                            <p><strong>{order.title}</strong></p>
                                            <p>{order.description.slice(0, 20)}</p>
                                            <p>: {order.deadline}</p>
                                            <p>: {order.status}</p>
                                            <p>: {order.executor ? `${order.executor.fullName} (ID: ${order.executor.id})` : ' '}</p>
                                        </div>
                                        <button className="btn btn-primary" onClick={() => navigate(`/orders/${order.id}`)}>
                                            
                                        </button>
                                    </li>
                                ))}
                        </ul>
                    ) : (
                        <p>  .</p>
                    )}
                        <FinishedOrdersDropdown finishedOrders={finishedOrders} navigate={navigate} />

                </>

            )}

            {/*   */}
            <Modal show={showModal} onHide={handleCloseModal}>
                <Modal.Header closeButton>
                    <Modal.Title>   </Modal.Title>
                </Modal.Header>
                <Modal.Body>
          <textarea
              placeholder="  ..."
              value={note}
              onChange={(e) => setNote(e.target.value)}
              className="form-control"
          />
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleCloseModal}>
                        
                    </Button>
                    <Button variant="primary" onClick={handleRequestSubmit}>
                         
                    </Button>
                </Modal.Footer>
            </Modal>
        </PageLayout>
    );
};

export default OrdersPage;
