import React, { useEffect, useState } from 'react';
import PageLayout from '../components/PageLayout'; //   
import UsersModeration from './UsersModeration';
import OrdersModeration from './OrdersModeration';
import { toast } from 'react-toastify';

const AdminPanel = () => {
    const [requests, setRequests] = useState([]); //  
    const [loadingRequests, setLoadingRequests] = useState(true); //   

    //     
    useEffect(() => {
        const fetchRequests = async () => {
            try {
                const response = await fetch('/api/requests/pending', {
                    method: 'GET',
                    headers: { 'Content-Type': 'application/json' },
                });

                if (!response.ok) throw new Error('   ');
                const data = await response.json();
                setRequests(data);
            } catch (error) {
                toast.error(error.message);
            } finally {
                setLoadingRequests(false);
            }
        };

        fetchRequests();
    }, []);

    //  
    const handleApproveRequest = async (requestId) => {
        try {
            const response = await fetch(`/api/requests/${requestId}/approve`, {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
            });

            if (!response.ok) throw new Error('   ');
            toast.success('  ');
            setRequests((prev) => prev.filter((req) => req.id !== requestId));
        } catch (error) {
            toast.error(error.message);
        }
    };

    //  
    const handleRejectRequest = async (requestId) => {
        try {
            const response = await fetch(`/api/requests/${requestId}/status`, {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ status: 'REJECTED' }),
            });

            if (!response.ok) throw new Error('   ');
            toast.success('  ');
            setRequests((prev) => prev.filter((req) => req.id !== requestId));
        } catch (error) {
            toast.error(error.message);
        }
    };

    return (
        <PageLayout title="-">
            <div className="admin-section">
                {/*   */}
                <section className="moderation-section">
                    <UsersModeration />
                </section>

                <hr />

                {/*   */}
                <section className="moderation-section">
                    <OrdersModeration />
                </section>

                <hr />

                {/*    */}
                <section className="moderation-section">
                    <h3> </h3>
                    {loadingRequests ? (
                        <p> ...</p>
                    ) : requests.length > 0 ? (
                        <ul className="list-group">
                            {requests.map((request) => (
                                <li key={request.id} className="list-group-item d-flex justify-content-between align-items-center">
                                    <div>
                                        <p><strong> ID:</strong> {request.order.id}</p>
                                        <p><strong>:</strong> {request.executor.fullName}</p>
                                        <p><strong>:</strong> {request.note}</p>
                                    </div>
                                    <div>
                                        <button
                                            className="btn btn-success btn-sm me-2"
                                            onClick={() => handleApproveRequest(request.id)}
                                        >
                                            
                                        </button>
                                        <button
                                            className="btn btn-danger btn-sm"
                                            onClick={() => handleRejectRequest(request.id)}
                                        >
                                            
                                        </button>
                                    </div>
                                </li>
                            ))}
                        </ul>
                    ) : (
                        <p>   .</p>
                    )}
                </section>
            </div>
        </PageLayout>
    );
};

export default AdminPanel;
