import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import PageLayout from '../components/PageLayout';
import { toast } from 'react-toastify';

const OrderDetails = () => {
    const { id } = useParams(); //  ID   URL
    const [order, setOrder] = useState(null);
    const [loading, setLoading] = useState(true);
    const navigate = useNavigate();

    useEffect(() => {
        const fetchOrderDetails = async () => {
            try {
                const response = await fetch(`/api/orders/${id}`, {
                    method: 'GET',
                    headers: { 'Content-Type': 'application/json' },
                });

                if (!response.ok) throw new Error('    ');
                const data = await response.json();
                setOrder(data);
            } catch (error) {
                toast.error(error.message);
            } finally {
                setLoading(false);
            }
        };

        fetchOrderDetails();
    }, [id]);

    if (loading) return <PageLayout title=" "><p>...</p></PageLayout>;

    if (!order) return <PageLayout title=" "><p>  </p></PageLayout>;

    return (
        <PageLayout title=" ">
            <h3>ID: {order.id}</h3>
            <h5>Title: {order.title}</h5>
            <p><strong>:</strong></p>
            <p>{order.description}</p>
            <p><strong>:</strong> {order.deadline}</p>
            <p><strong>:</strong> {order.status}</p>
            <p><strong>:</strong> {order.executor ? `${order.executor.fullName} (ID: ${order.executor.id})` : ' '}</p>
            <p><strong>: </strong>{order.client ? `${order.client.fullName} (ID: ${order.client.id})` : ' '} </p>

            <button className="btn btn-secondary" onClick={() => navigate(-1)}></button>
        </PageLayout>
    );
};

export default OrderDetails;
