import React, { useContext } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { UserContext } from '../UserContext';

const Header = () => {
    const { user, logout } = useContext(UserContext);
    const navigate = useNavigate();

    const handleLogout = () => {
        logout(); //   
        navigate('/'); //    
    };

    return (
        <header className="bg-dark text-white p-3">
            <nav className="container d-flex justify-content-between align-items-center">
                {/*   */}
                <Link to="/" className="text-white text-decoration-none"></Link>

                {/*     */}
                {user ? (
                    <>
                        <div className="d-flex align-items-center">
                            <Link to="/orders" className="text-white text-decoration-none me-3"> </Link>
                            {(user.role === 'CLIENT' || user.role === 'ADMIN') && (
                                <Link to="/orders/create" className="text-white text-decoration-none me-3"> </Link>
                            )}
                            {user.role === 'ADMIN' && (
                                <Link to="/admin" className="text-white text-decoration-none me-3"> </Link>
                            )}
                        </div>

                        {/*     */}
                        <div className="d-flex align-items-center">
                            <span className="me-3">, {user.fullName}!</span>
                            <button className="btn btn-danger btn-sm" onClick={handleLogout}></button>
                        </div>
                    </>
                ) : (
                    <>
                        {/*     */}
                        <div>
                            <Link to="/login" className="text-white text-decoration-none me-3"></Link>
                            <Link to="/register" className="text-white text-decoration-none"></Link>
                        </div>
                    </>
                )}
            </nav>
        </header>
    );
};

export default Header;
